.subtotal{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width:300px;
    height:200px;
    padding: 20px;
    background-color: rgb(242, 239, 239);
    border: 1px solid white;
    border-radius: 3px;
    margin-left: 5px;
    align-items:left;
}



.subtotal__gift{
    display: flex;
    align-items: center;
}
.subtotal__gift > input{
    margin-right: 5px;
}
.subtotal > button{
    background-color: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height:30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29 ;
    color: #111;
}