.login{
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 100px;
}

.login__logo{
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    height: 50px;
    margin-right: auto;
    margin-left: auto;
}

.login__container{
    width: 300px;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;

}

.login__container > h1{
    font-weight: 500;
    margin-bottom: 20px;
}

.login__container > form > input{
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}
.login__container > form > h5{
    margin-bottom: 10px;
    align-items: left;
}
.login__container  > p{
   margin-bottom: 15px;
   font-size: 12px;
}

.login__sign{
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;

}

.login_input{
    width: 100%;
}

.login__register{
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;

}